export default {
	data() {
		return {
			isFocus: false,
			scrollHeight: 'auto', // 内容区域高度
			scrollViewId: '', // 滚动到最底部
			voicePlayingId: '', // 正在播放的消息ID
			content: '',
			chatToken:null,
			list:[]
		}
	},
	async onLoad(option) {
		this.chatToken= option.chatToken;
		// 初始化内容高度
		this.setScrollHeight();

		this.$bus.$on('sendMsg',this.popMessage)
	},
	methods: {
		send(){
			this.pushMessage();
			this.content=""
		},
		async pushMessage(){
			const content = this.content;
			await this.$API.sendMsg({
				chatToken:this.chatToken,
				msg:content
			})
			
			this.list.push({
				source:"mine",
				avatarUrl:"/static/images/pyy.png",
				nickName:"深圳彭于晏",
				msg:content
			})
			
			this.$nextTick(() => {
				this.initScrollBar()
			})
		},
		
		popMessage(info){
			console.log(info)
			this.list.push({
				source:"friend",
				avatarUrl:"/static/images/wyz.png",
				nickName:"泉州吴彦祖",
				msg:info.msg
			});
			
			this.$nextTick(() => {
				this.initScrollBar()
			})
		},
		// 初始化滚动
		initScrollBar() {
			setTimeout(() => {
				this.scrollViewId = ''
				setTimeout(() => {
					this.scrollViewId = 'bottom'
					setTimeout(() => {
						this.scrollViewId = ''
					}, 100)
				}, 100)
			}, 100)
		},
		// 监听输入聚焦
		inputFocus(e) {
			this.setScrollHeight(e.detail.height)
			this.initScrollBar()

			uni.onKeyboardHeightChange(res => {
				this.setScrollHeight(res.height)
				this.initScrollBar()
			})
		},
		// 设置scroll的高度
		setScrollHeight(descHeight = 0) {
			this.scrollHeight = `calc(100vh - 110rpx - ${descHeight}px)`
		}
	},
}
